import { sha512 } from 'hash.js';
import * as BN from 'bn.js';

const PLACEHOLDER = '';

const SMILE = [
  "",    "",      "",    "", "",    "",    "", "",
  "",    "",   "", "", "",    "",    "",      "",
  "",    "",      "",    "", "",    "",    "",   "",
  "", "",      "",    "", "",    "",    "",      "",
  "", "", "",    "", "",    "",    "",      "",
  "",    "",      "",    "", "",    "",    "",      "",
  "",    "",      "",    "", "",    "",    "",      "",
  "",    "",      "",    "", "", "", "",      "",
  "",    "",   "", "", "",    "",    "",      "",
  "",    "",      "", "", "",    "",    "",      "",
  "",    "",      "",    ""];

const GESTURE = [
  "", "",      "", "",    "", "", "",
  "", "", "", "", "", "", "",
  "", "", "", "", "", ""];

const ANIMAL = [
  "",    "",    "",    "", "",    "",    "",   "", "", "",
  "", "",    "",    "", "",    "",    "",   "", "", "",
  "",    "",    "",    "", "", "",    "",   "", "", "",
  "",    "", "",    "", "", "", "",   "", "", "",
  "",    "",    "",    "", "",    "",    "",   "", "", "",
  "",    "",    "",    "", "",    "",    "",   "", "", "",
  "",    "",    "", "", "",    "",    ""];

const FOOD = [
  "", "", "",      "", "", "",    "", "", "",    "",    "",
  "", "", "",      "", "", "", "", "", "",    "",    "",
  "", "", "",      "", "", "", "", "", "", "", "",
  "", "", "",      "", "", "",    "", "", "",    "",    "",
  "", "", "",      "", "", "",    "", "", "",    "", "",
  "", "", "", "", "", "",    "", "", "",    "",    ""];

const OBJECT = [
  "", "",      "",       "", "",   "", "",
  "",   "",   "",    "",      "",      "",    "",
  "",   "",   "",       "", "",      "",    "",
  "",      "",   "",       "", "",      "",    "",
  "",      "",      "",    "",      "",   "",    "",
  "",      "",      "",       "",      "", "",    "",
  "",      "", "",       "",      "",      "", "",
  "",      "",   "",       "", "",      "",    "",
  "",      "",      "",    "",      "",      "",    "",
  "",   "",      "",    "",   "",   "", "",
  "",      "",      "",       "", "",      "", "",
  "",      "",      "",       "",      "",      "", "",
  "",      "",      "",    "",      "",      "",    "",
  "",      "",      "", "",      "", "", "",
  "",      "",      ""];

const ALPHABET = [ SMILE, GESTURE, ANIMAL, FOOD, OBJECT ];

export default function compute(input) {
  if (input === '') {
    return PLACEHOLDER;
  }

  const text = 'derivepass/' + input;
  const digest = sha512().update(text).digest();

  const fingerprint = new BN(digest.slice(0, 8), 'le');

  let out = '';
  for (const list of ALPHABET) {
    const index = fingerprint.modn(list.length);
    fingerprint.idivn(list.length);

    out += list[index];
  }
  return out;
}
